<template>
<div class="card">
  <header class="card-header">
    <p class="card-header-title is-size-4">
      Menubar items:
    </p>
  </header>
  <div class="card-content">
    <div class="content">
        <div class="columns">
            <div class="column is-5">
                <div class="field">
                    <label class="label">[URL]:</label>
                    <p class="control">
                        <input id="url" class="input" name="url" v-model="link" type="text" placeholder="[LINK]">
                    </p>
                </div>
            </div>
            <div class="column is-5">
                <div class="field">
                    <label class="label">Title:</label>
                    <p class="control">
                        <input id="bar_title" class="input" name="title" v-model="title" type="text" placeholder="Title">
                    </p>
                </div>
            </div>
            <div class="column is-2">
                <div class="field">
                <label class="label">Submit</label>
                <button id="" class="button" @click="addPage">Save</button>
                </div>
            </div>
        </div>
        <table class="table is-striped" style="word-break:break-all;">
            <thead>
                <th>ID</th>
                <th>Caption</th>
                <th>URL</th>
                <th>Actions</th>
            </thead>
            <tbody>
                <tr v-for="(page) in pages" :key="page.id">
                    <th>{{page.id}}</th>
                    <td>{{page.title}}</td>
                    <td>{{page.link}}</td>
                    <td><a @click="deleteById(page.id)">Delete</a></td>
                </tr>
            </tbody>
        </table>
    </div>
  </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            pages:{},
            link:'',
            title:'',
            req:{}
        }
    },

    methods: {
        deleteById(id) {
            this.$axios.post('/pagesmgmt/delete',{
                id:id
            }).then((response)=>{
                console.log(response)
                this.onLoad();
                this.$notify('Done.')
            })
        },

        addPage() {
            this.req.title = this.title;
            this.req.link = this.link;
            this.$axios.post('/pagesmgmt/add', this.req).then( (response)=>{
                this.onLoad();
                this.$notify('Done.')
            })
        },

        onLoad() {
            this.$axios.get('/pagesmgmt').then(resp => {
            console.log(resp.data)
            this.pages = resp.data.data;
            })
        }


    },

    created() {
        this.onLoad();
    }
    
}
</script>